$cr_quote_marker_size: 2.15rem;
.cr-quote {
    opacity: 0;
    display: inline-block;
    position: relative; 
    text-align: center;
    padding: 2rem 2.5rem 0.8rem 3.2rem;
    margin: 2rem 1rem 1rem;    
    @include respond-to(medium){
        padding-left: 3.8rem;
    }
    &.active{
        @include animation(fadeIn,1s, $delay:0.8s)
    }

    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: $cr_quote_marker_size;
        width: $cr_quote_marker_size + 0.75;
        background:$cr_quote_marker_img no-repeat center/cover;
    }
    
    &::after{
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        height: ($cr_quote_marker_size/1.4);
        width: ($cr_quote_marker_size/1.4) + 0.5;
        background:$cr_quote_marker_img no-repeat center/cover;
        transform: rotate(180deg);
    }
    &__text {
        font-size: 1.35rem;
        font-weight: 200;
        font-style: italic;
        color: $disable_font_color;
        @include respond-to(medium){
            font-size: 1.5rem;
        }
    }
}